<template>
  <div class="wrapper">
    <app-header @openBurger="isOpenBurger = !isOpenBurger" class=" relative z-30"></app-header>
    <app-burger @changePage="selectPage" :is-open-burger="isOpenBurger"></app-burger>

    <app-first-page class="py-5 px-5" v-if="selectedPage == 'first'"></app-first-page>

    <app-second-page class="py-5 px-5" v-if="selectedPage == 'second'"></app-second-page>

    <app-third-page class="py-5 px-5" v-if="selectedPage == 'third'"></app-third-page>

    <app-fouth-page class="py-5 px-5" v-if="selectedPage == 'fourth'"></app-fouth-page>

    <app-reviews-page :reviews="reviews" class="py-5 px-5" v-if="selectedPage == 'reviews'" ></app-reviews-page>
  </div>

</template>

<script>
import AppHeader from "@/components/AppHeader";
import AppBurger from "@/components/AppBurger";
import AppFirstPage from "@/components/pages/AppFirstPage";
import AppSecondPage from "@/components/pages/AppSecondPage";
import AppThirdPage from "@/components/pages/AppThirdPage";
import AppFouthPage from "@/components/pages/AppFouthPage";
import AppReviewsPage from "@/components/pages/AppReviewsPage";
export default {
  name: 'App',
  data() {
    return {
      isOpenBurger: false,
      selectedPage: "first",
      reviews: {}
    }
  },
  methods: {
    selectPage(number) {
      this.selectedPage = number
    }
  },
  mounted() {
    fetch("https://vue-compete-default-rtdb.europe-west1.firebasedatabase.app/reviews.json")
        .then(data => data.json())
        .then(data => this.reviews = data)

  },
  components: {
    AppHeader,
    AppBurger,
    AppFouthPage,
    AppFirstPage,
    AppSecondPage,
    AppThirdPage,
    AppReviewsPage
  }
}
</script>

<style lang="scss">
.burger__menu {
  right: -400px;
  height: 100vh;
  transition: all ease 0.3s;
}
.burger__active {
  right: 0;
  transform: translateX(0);
  transition: all ease 0.3s;
}

</style>
